
import _ from 'lodash'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import {mapGetters, mapActions} from 'vuex'
export default {
name: 'Records',
data() {
return {

};
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
mounted() {
if(this.records.length == 0)
this.getAllRecords({
alert: this.$vux.alert,
cb:() => {

}});
},
computed: {
...mapGetters(['records']),
},
methods: {









formatAmount({ RecordType, Amount }) {
return (RecordType == '收入' ? '+' : '-') + numberComma(Amount.toFixed(2))
},
removeRecord(index) {
var record = this.records[index];
this.$vux.confirm.show({
title: '警告',
content: '将删除此项目, 是否继续?',
onConfirm: () => {
this.deleteRecord({
data: { recordID: record.RecordID },
alert: this.$vux.alert,
cb: res => {
if(res.success){
this.$vux.toast.show({
text: '删除成功！',
type: 'success',
});
}
}
})










}
})












},
...mapActions(['deleteRecord', 'getAllRecords'])
}
};




